@media screen and (max-width: 1000px) {
    .expertise-container {
        flex-direction: column;
        align-items: center;
        margin: 0rem 2rem;
    }

    .expertise-container div:first-child {
        border-bottom: unset;
    }

    .development-container {
        border-right: solid rgba(255, 255, 255, 0.664) 4px !important;
    }
}